<template>
  <div id="app">
    <div class="messages dis-flex" id="im-record">
      <div
        v-for="msg in currentMessage"
        :key="msg.id"
        class="d-flex gap-2 w-75 mb-4 bubble"
        :class="{ self: 1 === msg.type }">
        <div class="text">
          <div class="chatfield p-2">
            <span v-html="convertToLink(msg.msg)"></span>
          </div>
        </div>
      </div>
    </div>
    <div class="footer im-input dis-flex">
      <input type="text" v-model="msg" placeholder="请输入内容">
      <button @click="send">发送</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      title: '',
      switchType: 1,
      uid: '',
      nickname: '',
      socket: '',
      msg: '',
      messageList: [],
    }
  },
  mounted() {
    let vm = this;
    vm.uid = "CarryCloud";
    vm.nickname = "PC";
    vm.conWebSocket();

    document.onkeydown = function (event) {
        var e = event || window.event;
        if (e && e.keyCode == 13) { //回车键的键值为13
            vm.send()
        }
    }
    window.onbeforeunload = function (e) {
      vm.socket.send(JSON.stringify({
        uid: vm.uid,
        type: 1
      }));
    }
  },
  computed: {
    currentMessage() {
      let vm = this;
      let data = vm.messageList.filter(item => {
        if (item.type !== 0) {
          return item.msg;
        }
      })
      data.map(item=>{
        item.status = 0
        return item;
      })
      return data;
    },
    usersUnRead(){
      return this.messageList.some(item => {
        return item.bridge.length && item.status === 1
      })
    },
    currentUserList() {
      let vm = this;
      vm.users.map(user=>{
        user.unread = this.messageList.filter(item=>{
          return item.bridge.length && item.uid === user.uid && item.status === 1
        }).length
        return user;
      })
      return vm.users;
    }
  },
  methods: {
    scrollBottom() {
      const messages = document.getElementsByClassName('messages')[0];

      if (messages) {
        messages.scrollTop = messages.scrollHeight;
      }
    },
    send(){
      this.msg = this.msg.trim();
      if(!this.msg){
        return;
      }

      this.sendMessage(1, this.msg);
      this.scrollBottom();
    },
    sendMessage(type, msg){
      if (this.socket.readyState == WebSocket.OPEN) {
        let obj = {
          id: new Date().getMilliseconds(),
          type: type,
          msg: msg
        }
        this.messageList.push(obj);
        this.socket.send(JSON.stringify(obj));
        this.msg = '';
      } else {
        let obj = {
          type: 1,
          msg: "请刷新页面后再试"
        }
        this.messageList.push(obj);
      }
    },
    convertToLink: function (text) {
      const URLMatcher =
        /(?:(?:https?|ftp|file):\/\/|www\.|ftp\.)(?:\([-A-Z0-9+&@#/%=~_|$?!:,.]*\)|[-A-Z0-9+&@#/%=~_|$?!:,.])*(?:\([-A-Z0-9+&@#/%=~_|$?!:,.]*\)|[A-Z0-9+&@#/%=~_|$])/gim;

      return text.replace(
        URLMatcher,
        (text) =>
          `<a class="text-primary" rel="noreferrer" target="_blank" href="${text}">${text}</a>`,
      );
    },
    conWebSocket(){
      let vm = this;
      if(window.WebSocket){
        let app = document.getElementById('app');
        let server_url = "ws://127.0.0.1:8081"
        if (app != undefined && app != null) {
          let tmp = app.getAttribute('server_url');
          if (tmp != undefined && tmp != null && tmp != "") {
            server_url = tmp
          }
        }

        vm.socket = new WebSocket(server_url);
        let socket = vm.socket;

        socket.onopen = function(e){
          console.log("连接服务器成功");

          vm.sendMessage(0, "Peer Online")
        }
        socket.onclose = function(e){
          console.log("服务器关闭");
        }
        socket.onerror = function(){
          console.log("连接出错");
          vm.sendMessage(1, "连接" + server_url + "出错, 请刷新页面后再试");
        }
        // 接收服务器的消息
        socket.onmessage = function(e){
          let msg = JSON.parse(e.data)
          console.log("onmessage type:" + msg.type + ", msg:" + msg.msg);
          vm.messageList.push(msg);
          vm.$nextTick(function(){
            var div = document.getElementById('im-record');
            div.scrollTop = div.scrollHeight;
          })
        }
      }
    }
  }
}
</script>


